
            @import "src/sass/_variables.scss";
            @import "src/sass/_base.scss";
            @import "src/sass/_mixins.scss";
            @import "src/sass/_typography.scss";
            @import "src/sass/_utilities.scss";
            @import "src/sass/components/_buttons.scss";
            @import "src/sass/components/_inputs.scss";
            @import "src/sass/components/_form.scss";
          
.addaddressform {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 8rem);
    grid-auto-rows: 5rem;
    justify-content: center;
    width: 70%;
    margin: 0 auto;
    &__name {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        & input {
            width: 30rem;
        }
    }
    
    &__address {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        & input {
            width: 30rem;
        }
    }

    &__apartment {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
        & input {
            width: 30rem;
        }
    }
    
    &__city {
        grid-column: 1 / span 2;
        grid-row: 4 / span 1;
        & input {
            width: 16rem;
        }
    }

    &__state {
        grid-column: 3 / span 1;
        grid-row: 4 / span 1;
        & input {
            width: 8rem;
        }
    }

    &__postalCode {
        grid-column: 4 / span 1;
        grid-row: 4 / span 1;
        & input {

            width: 8rem;
        }
    }
    &__phone {
        grid-column: 1 / -1;
        grid-row: 5 / span 1;
        & input {
            width: 30rem;
        }
    }
}
