.btn--navbar {
  background: transparent;
  cursor: pointer;
  @include flexCenter(row);
  text-transform: uppercase;
  border: none;
  text-decoration: none;
  letter-spacing: 0px;
  color: white;
  padding: 5px 10px;
  width: max-content;
  position: relative;

  &:after {
    position: absolute;
    display: block;
    height: 100%;
    top: 0;
    bottom: 0;
    width: 100%;
    content: "";
    background-color: $color-primary;
    transform: skew(20deg);
    z-index: -1;
  }



  &:hover:after,
  &--active:after {
    display: none;
  }

  &:hover,
  &--active {
    background-color: rgb(0, 0, 0);
    color: white;
    transform: skew(20deg) scale(1.05);
  }
  &.markwithborder::after {
    content: "";
    display: block;
    bottom: 0;
    left: 0;
    width: 10rem;
    height: 2px;
    position: absolute;
    background-color: black;
  }
}

  .btn--navbar2 {
    background: transparent;
    @include flexCenter(row);
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    text-decoration: none;
    letter-spacing: 0px;
    color: black;
    padding: 10px;
    font-family: $font-family-2;
    width: max-content;
    border-radius: 3px;
    position: relative;
  
    &:hover,
    &--active {
      background-color: rgb(0, 0, 0);
      color: white;
      transform: scale(1.05);
    }
  }

  .btn--primary,
  .btn--primary:link,
  .btn--primary:visited {
    position: absolute;
    text-decoration: none;
    cursor: pointer;
    bottom: .5rem;
    left: 1.5rem;
    border: none;
    background-color: white;
    padding: .5rem 2rem;
    color: black;
    transition: all 0.2s;
    z-index: 10;

    &--none {
      display: none;
    }
    
    &:active {
      transform: translateY(-1px);
      box-shadow: 0 1px 1px rgba(black, 0.4);
    }
    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 3px 2px rgba(black, 0.4);
    }
    &:active {
      transform: translateY(-1px);
      box-shadow: 0 1px 1px rgba(black, 0.4);
    }
  }


  .btn--main {

    margin: 0 auto;
    font-weight: 200;
    z-index: 5;
    justify-self: center;
    font-size: 1em;
    letter-spacing: 0.1em;
    text-decoration: none;
    color: $color-button;
    display: inline-block;
    padding: 1rem 2rem 1rem 2rem;
    position: relative;
    border: 1px solid $color-button;
    border-radius: 20px;
    &::before {
      background: $color-button url(http://www.graphicartsunit.com/images/noise2.png);
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      border-radius: 20px;
      left: 0px;
      top: 0px;
      z-index: -2
    }
  }