.footer {
  height: 20rem;
  background-color: #333;
  color: white;
  @include flexCenter(row);
  width: 100%;
  gap: 2rem;
  padding-right: 2rem;
  margin: 0;
  align-items: center;
  
  &__logo {
    margin: 2rem;
    margin-right: auto;
    display: block;
    object-fit: contain;
    height: 18rem;
    width: 18rem;
    z-index: 1000;
  }

  &__info {
    gap: 5px;
    @include flexCenter(column);
    align-self: center;
    & a {
      color: white;
    }
  }

  &__contactinfo {
    display: grid;
    grid-template-columns: 10rem 20rem;
  }

}
